<!-- 月累计告警数 -->
<template>
	<div class="table_t01">
		<table>
			<tbody cellspacing="0" cellpadding="5px">
				<tr>
					<th v-for="item in static.thList">{{ item }}</th>
				</tr>
				<tr v-for="item in tdList">
					<td class="clo_1">{{ item.col1 }}</td>
					<td class="clo_2">{{ item.col2 }}</td>
					<td class="clo_3">{{ item.col3 }}</td>
				</tr>
			</tbody>
		</table>
	</div>
</template>

<script>
export default {
	name: '',
	props: {
		tdList:{
			default: function (){
				return [
					{
						"col1": "门窗异常",
						"col2": "16",
						"col3": "人民路123弄20号301室"
					},
					{
						"col1": "可燃气体泄露",
						"col2": "16",
						"col3": "人民路123弄20号301室"
					},
					{
						"col1": "可燃气体泄露",
						"col2": "16",
						"col3": "人民路123弄20号301室"
					},
					{
						"col1": "可燃气体泄露",
						"col2": "16",
						"col3": "人民路123弄20号301室"
					},
					{
						"col1": "可燃气体泄露",
						"col2": "16",
						"col3": "人民路123弄20号301室"
					},
					{
						"col1": "可燃气体泄露",
						"col2": "16",
						"col3": "人民路123弄20号301室"
					}
				]
			}
		},
		static: {
			default(){
				return {
					"thList": ["告警类型", "告警数", "地址"]
				}
			}
		}
	},
	data () {
		return {
		  
		}
	}
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="less">
	.table_t01{
		table{
			width: 100%;
			text-align: center;
		}
		
		tr{

		}
		th{
			line-height: 30px;
			padding: 10px 0;
			color: #00f4e6;
			font-size: 18px;
			white-space: nowrap;
			overflow: hidden;
			// border-right: 1px solid #103552;
			background-color: #1d3e50;
		}
		td.clo_3{
			text-align: left;
		}
		td{
			white-space: nowrap;
			overflow: hidden;
			border-bottom: 1px solid #103552;
			word-break: break-all;
			padding: 5px 0;
			line-height: 30px;
			color: #ececec;
			font-size: 14px;
		}
	}
	
</style>


