<!-- 出区域告警详情 -->
<template>
		<div class="table_02">
			<table>
				<tbody>
					<tr>
						<th v-for="item in static.thList">{{ item }}</th>
					</tr>
					<tr v-for="item in list">
						<td>{{ item.time_alert }}</td>
						<td>{{ item.address }}</td>
						<td>{{ item.time_leave }}</td>
						<td>{{ item.duration }}</td>
					</tr>
				</tbody>
			</table>
		</div>
</template>

<script>
export default {
	name: '',
	props: {
		list: {
			default: function () {
				return aged1.day_alert_district_leave_detail
			}
		},
		static: {
			default(){
				return {
					"thList": ["告警时间", "地址", "离开时间", "长时未归"]
				}
			}
		}
	},
	data () {
		return {
		  
		}
	}
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="less">
.table_02{
	table{
		width: 100%;
		height: 100%;
		text-align: center;
	}
	td, th{
		padding: 10px 0;
	}
	th{
		text-align: center;
		font-size: 25px;
		color: #00fff0;
	}

	tr:nth-child(odd){
		background-color: rgba(255, 255, 255, 0.08);
	}
	td{
		color: #ddd;
		font-size: 14px;
	}
}
</style>


