<!-- 规划部署 -->
<template>
	<div class="content flex">
    <div class="flex_item">
    	<p class="title">{{ static.title1 }}</p>
    	<p class="pic_box">
    		<img class="img1"  src="../../static/img/guihua.png" >
    		<a class="getvalue1">{{ static.num1 }}</a>
    	</p>
    	<p class="unit">{{ static.desc1 }}</p>
    </div>
    <div class="flex_item">
    	<p class="title">{{ static.title2 }}</p>
    	<p class="pic_box">
    		<img class="img1" src="../../static/img/laonianzhuhu.png" >
    		<a class="getvalue1">{{ static.num2 }}</a>
    	</p>
    	<p class="unit">{{ static.desc2 }}</p>
    </div>
	</div>
</template>

<script>
export default {
	name: '',
	props: {
		static: {
			default(){
				return {
					"title1": "规划部署",
					"desc1": "(户)",
					"title2": "已部署",
					"desc2": "(户)",
					"num1": 123,
					"num2": 222
				}
			}
		}
	},
	data () {
		return {
		  
		}
	}
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
	.flex{
		justify-content: space-around;
	}
	.flex_item{
		flex: 0 0 50%;
		text-align: center;
		position: relative;
	}
	.pic_box{
		position: relative;
		padding-top: 20px;
	}
	.content{
		margin:0;
		height: 265px;
		width: 798px;
	}
	
	.title{
		font-size:30px;
		color:#00ffee;
		margin-top:17px;
	}
	.unit{
		color: #fff;		
	}
	.img1{
		height: 140px;
	}
	.getvalue1{
		position: absolute;
		top: 50%;
		left: 50%;
		transform: translate(-50%, -20%);
	}
</style>


