<!-- 当日告警 -->
<template>
<div class="content">
  <div class="title">
    <img class="imgtit" src="../../static/img/chuanganjiance.png" />
    <span>{{ static.title }}</span>
    <img  class="imglight imglight1" src="../../static/img/red.png" />
    <span class="spanlight1">{{ static.descWarn }}</span>
    <img  class="imglight imglight2" src="../../static/img/green.png" />
    <span class="spanlight2">{{ static.descNor }}</span>
  </div>
  <div class="container" >
    <img src="../../static/img/jiance.png" />
    <div>
      <span class="text" 
      	v-for="(item, index) in static.thList"
				:class="'text'+(index+1)"
      >{{ item }}</span>

    </div>
    <span class="getvalue getvalue1">{{ oData.num_alert_door }}
    	<i :class="isAlert(oData.num_alert_door)"></i>
    </span>
    <span class="getvalue getvalue2">{{ oData.num_alert_combustible_gas }}<i :class="isAlert(oData.num_alert_combustible_gas)"></i></span>
    <span class="getvalue getvalue3">{{ oData.num_alert_fire }}<i :class="isAlert(oData.num_alert_fire)"></i></span>
    <span class="getvalue getvalue4">{{ oData.num_alert_fall }}<i :class="isAlert(oData.num_alert_fall)"></i></span>
  </div>
</div>

</template>

<script>
export default {
	name: '',
	props: {
		oData: {
			default(){
				return aged1.day_alert
			}
		},
		static: {
			default(){
				return {
					"title": "当日告警",
					"descWarn": "告警",
					"descNor": "正常",
					"thList": [ "门窗异常", "可燃气泄漏", "火灾警告", "室内摔倒"]
				}
			}
		}
	},
	data () {
		return {
		  
		}
	},
	methods: {
		isAlert(num){
			if ( +num > 0) {
				return 'red'
			}else {
				return 'green'
			}
		}
	}
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="less">
	.content{
		margin:0;
		height: 330px;
		width: 100%;
		border-radius: 10px;
	}
	
	.title{
		font-size:30px;
		color:#00ffee;
		margin-top:20px;
		margin-bottom:20px;
	}
	.imgtit{
		float:left;
		margin-left:19px;
		margin-right:38px;
		
	}
	.spanlight1,.spanlight2{
		float:right;
		font-size:20px;
		margin-right:-65px;
		margin-top:50px;
	}
	
	.imglight1,.imglight2{
		float:right;
		margin-right:23px;
		margin-top:-17px;			
	}
	
	.container{
		
		height: 185px;
		margin-left:60px;
		margin-top:70px;
	}
	
	.container>img{
		margin-left:0px;
		position:absolute;
		z-index:0;
	}
	
	.text{
		color:#ffffff;
		font-size:20px;
		float:left;	
		margin-left:50px;
		margin-top:38px;			
		position:absolute;
		z-index:1;
		white-space: nowrap;
		overflow: hidden;
	}
	.text2{
		margin-left:212px;
	}
	.text3{
		margin-left:390px;
	}
	.text4{
		margin-left:556px;
	}
	
	.getvalue{
		color:#ffffff;
		font-size:24px;
		float:left;
		width:60px;
		margin-top:90px;
		margin-left:107px;
		text-align:center;
		position: relative;
		i{
			position: absolute;
			bottom: -40px;
			right: 10px;
			display: inline-block;
			width: 40px;
			height: 50px;
			background-color: #000;
			&.red{
				background: url(../../static/img/red.png) no-repeat center;
			}
			&.green{
				background: url(../../static/img/green.png) no-repeat center;
			}
			background-size: contain;
		}
	}
	.getvalue1{
		margin-left:60px;		
	}

</style>


