<!-- 告警高发阶段： -->
<template>
<div class="content">
	<div class="title">
		<span>{{ static.title }}</span>
	</div>
   <div class="container" style=""></div>
</div>
</template>
<!-- day_tendency_alert_interval -->
<script>

function initChart(ele, list, outOpt){
	outOpt = outOpt || {};
	var myData = formatList(list);

	var myChart ;
	var dom = ele.querySelector('.container');
	if ( $(dom).find('canvas').length > 0 ) {
	  myChart = echarts.getInstanceByDom( dom )
	}else{
	  myChart = echarts.init( dom );
	}
	
	var option = null;
	option = {
		grid: {
			bottom: '10%',
			top: '0%',
			left: '0%'
		},
		tooltip: {
			trigger: 'axis',
			formatter: '{b} : {c}'
		},
		xAxis: {
			type: 'category',
			data: myData.axis,
			axisTick: {
				show: false
			},
			axisLabel: {
				textStyle: {
					fontSize: 14,
					color: '#cccccc',
				}
			},
			axisLine: {
				lineStyle: {
					color: '#aaaaa9',

				}
			}
		},
		yAxis: {
			type: 'value',
			boundaryGap: [0, 0.3],
			axisTick: {
				show: false
			},
			splitLine: {　
				show: false,
			},
			axisLine: {
				lineStyle: {
					color: '#aaaaa9',

				}
			},
			axisLabel: {
				formatter: function() {
					return "";
				}
			}
		},
		series: [{
			name: '高发',
			data: myData.data1,
			type: 'line',
			smooth: true,
			//symbol: 'circle',
			itemStyle: {
				normal: {
					areaStyle: {},
					color: new echarts.graphic.LinearGradient(
						0, 0, 0, 1, [{
							offset: 0,
							color: 'rgba(41,207,196, 0.2)'
						}, {
							offset: 1,
							color: 'rgba(41,207,196, 1)'
						}]
					),
					lineStyle: {
						color: "#44bbbb"
					}
				}
			},
		}]
	};

	if (option && typeof option === "object") {
	    myChart.setOption(option, true);
	    myChart.setOption(outOpt, false);
	}
}
function formatList( list ){
    var axis = [];
    var data1 = [];
    var data2 = [];
    list.forEach( function(item, index) {
        axis.push( item.interval );
        data1.push( item.num_alert );
    });
    return {
        axis,
        data1
    }
}

export default {
	name: '',
	props: {
		outOpt:{
			default(){
				return {

				}
			}
		},
		list: {
			default(){
				return [
					{
						"interval": "00",
						"num_alert": 40
					}
				]
			}
		},
		static: {
			default(){
				return {
					"title": "--"
				}
			}
		}
	},
	data () {
		return {
		  
		}
	},
	mounted(){
		initChart(this.$el, this.list, this.outOpt);
	},
	watch: {
    list: function (val, oldV) {
        initChart(this.$el , this.list, this.outOpt);
    }
	}
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.title{
	margin-left:45px;
	margin-top:17px;
	font-size:30px;
	
	color:#00ffee;
}
.content{
	margin:0;
	/*border: solid 1px rgba(56, 174, 225, 0.25);*/
	height: 512px;
	width: 100%;
	/*background-color:#001122*/
}
.container{
	margin-top:73px;
	height: 363px;
	width:80%;
	margin-left:10%;
}
</style>


