<!-- 传感器部署 -->
<template>
	<div class="content">
		<div class="title">
			<img class="imgtit" src="../../static/img/yibushulogo.png" />
			<span>{{ static.title }}</span>
		</div>
		<div class="container" style="">
			<img src="../../static/img/bushu.png" />
			<span class="getvalue">{{ static.num }}</span>
			<span class="getvalue">{{ static.desc }} </span>
		</div>
	</div>
</template>

<script>
export default {
	name: '',
	props: {
		num: {
			default: '--',
		},
		static: {
			default(){
				return {
					title: '传感器部署',
					desc: '传感器(户)',
					num: '--'
				}
			}
		}
	},
	data () {
		return {
		  
		}
	}
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
	.content{
		margin:0;
		height: 265px;
		width:400px;
		/*background-color:#001122*/
		/*border: solid 1px rgba(56, 174, 225, 0.71);*/
		/*opacity: 0.71;*/
	}
	.title{
		font-size:30px;
		color:#00ffee;
		margin-top:17px;
		margin-bottom:20px;
	}
	.imgtit{
		float:left;
		margin-left:24px;
		margin-right:52px;
	}
	.container{
		height: 185px;
		margin-left:110px;
	}
	.container>div>span{
		float:left;
		color:#ffffff;
		margin-left:-66px;
		margin-top:150px;
		font-size:24px;
	}
	.container>img{
		margin-left:0px;
		position:absolute;
		z-index:0;
	}
	.getvalue{
		color:#ffffff;
		font-size:24px;
		float:left;
		margin-top:60px;
		display: inline-block;
		width: 160px;
		text-align: center;
	}
</style>


