<!-- 出区域告警排名（一键报警） -->
<template>
	<div class="content">
		<div class="title">
			<img class="imgtit" src="../../static/img/baojing.png" />
			<span>{{ static.title }}</span>
		</div>
		<div class="container" style=""></div>
	</div>
</template>

<script>
import {arrayCopy} from '../assets/js/assets.com.js';

function initChart(ele, list){
	var myData = formatList(list);
	
	var myChart ;
	var dom = ele.querySelector('.container');
	if ( $(dom).find('canvas').length > 0 ) {
	  myChart = echarts.getInstanceByDom( dom )
	}else{
	  myChart = echarts.init( dom );
	}

	var option = null;

	option = {
		tooltip: {
			trigger: 'axis',
			axisPointer: {
				type: 'shadow'
			}
		},
		grid: {
			top: '0%',
			left: '3%',
			right: '4%',
			bottom: '0%',
			containLabel: true
		},
		xAxis: {
			type: 'value',
			boundaryGap: [0, 0.01],
			axisLine: {
				show: false
			},
			splitLine: {
				show: false
			},
			axisLabel: {
				show: false
			},
			axisTick: {
				show: false
			},

		},
		yAxis: {
			type: 'category',
			data: myData.axis,
			axisLine: {
				show: false
			},
			splitLine: {
				show: false
			},

			axisTick: {
				show: false
			},
			axisLabel: {
				textStyle: {
					fontSize: 14,
					color: '#ffffff'
				}
			}
		},
		series: [{
			name: '区域报警',
			type: 'bar',
			data: myData.data1,
			barWidth: 20,
			//配置样式
			itemStyle: {
				//通常情况下：
				normal: {　　　　　　　　　　　　 //每个柱子的颜色即为colorList数组里的每一项，如果柱子数目多于colorList的长度，则柱子颜色循环使用该数组
					color: function(params) {
						var colorList = ['#8e3239', '#9c6860', '#9c6860', '#477e8f', ];
						return colorList[params.dataIndex];
					},
					label: {
						show: true,
						position: 'right',

					}

				},
				//鼠标悬停时：
				emphasis: {
					shadowBlur: 10,
					shadowOffsetX: 0,
					shadowColor: 'rgba(0, 0, 0, 0.5)',
					barBorderWidth: 1,
					label: {
						show: true
						// position: 默认自适应，水平布局为'top'，垂直布局为'right'，可选为
						//           'inside'|'left'|'right'|'top'|'bottom'
						// textStyle: null      // 默认使用全局文本样式，详见TEXTSTYLE
					}
				}
			},
		}, ]
	};

	if (option && typeof option === "object") {
		myChart.setOption(option, true);
	}
}

function formatList( list ){
	var _list = arrayCopy(list);
	_list.sort( function(item1, item2) {
		return item1.num_alert - item2.num_alert
	});
	var axis = [];
	var data1 = [];
	var data2 = [];
	_list.forEach( function(item, index) {
		axis.push( item.district_name );
		data1.push( item.num_alert );
	});
	return {
		axis,
		data1
	}
}

export default {
	name: '',
	props: {
		list: {
			default(){
				return aged1.day_alert_district_leave_topn
			}
		},
		static: {
			default(){
				return {
					"title": "出区域告警排名（一键报警）"
				}
			}
		}
	},
	data () {
		return {
		  
		}
	},
	mounted(){
		initChart( this.$el, this.list )
	},
	watch: {
    list: function (val, oldV) {
      initChart(this.$el , this.list);
    }
	}
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
	.title{
		padding-left:20px;
		padding-top:17px;
		font-size:30px;
		
		color:#00ffee;
	}
	.container{
		margin-top:33px;
		height: 220px;
		width:;
		margin-left:48px;
	}
	.content{
		margin:0;
		border-radius: 10px;
		height: 330px;
		width:690px;
		background-color: rgba(4, 34, 65, 0.5);
	}
	.imgtit{
		float:left;
		margin-right:24px
	}
</style>


