<template>
	<page-box class="aged">
		<!-- 左边 -->
		<div class="c_left clear" slot="left">
			<div class="left_1 clear">
				<left0101 
					:static="static.left0101"
				></left0101>
				<left0102 class="left_1_2"
					:static="static.left0102"
				></left0102>
			</div>
			<div class="left_2">
				<div class="left_2_1">
					<warn-sum 
						:num="static.warnSum.num"
						:static="static.warnSum"
					></warn-sum>
				</div>
				<table-t01 class="left_2_2"
					:tdList="static.tableT01.dataList"
					:static="static.tableT01"
				></table-t01>
			</div>
			<div class="left_3">
				<div class="left_3_1">
					<warn-today
						:oData="static.warnToday.data"
						:static="static.warnToday"
					></warn-today>
				</div>
				<div class="left_3_2">
					<table-order-day
						:static="static.tableOrderDay"
					></table-order-day>
				</div>
			</div>
		</div>
		<!-- 中间 -->
		<div class="c_center clear" slot="center">
			<div class="center_1">
				<iframe :src="static.centerTop01.iframe" class="cont"></iframe>
			</div>
			<div class="center_2">
				<warn-area-bar
					:list="static.warnAreaBar.dataList"
					:static="static.warnAreaBar"
				></warn-area-bar>
			</div>
			<div class="center_3">
				<warn-area-barh
					:list="static.warnAreaBarh.dataList"
					:static="static.warnAreaBarh"
				></warn-area-barh>
			</div>
		</div>
		<!-- 右边 -->
		<div class="c_right clear" slot="right">
			<div class="right_1">
				<warn-occur-area
					:list="static.warnOccurArea.dataList"
					:static="static.warnOccurArea"
					:outOpt="warnOccurAreaOpt"
				></warn-occur-area>
			</div>
			<div class="right_2">
				<warn-occur-time
					:list="static.warnOccurTime.dataList"
					:static="static.warnOccurTime"
				></warn-occur-time>
			</div>
			<div class="right_3">
				<div class="com_title">
					{{static.right_3.title}}
				</div>
				<table-t02 class="r_3_table"
					:list="static.tableT02.dataList"
					:static="static.tableT02"
				></table-t02>
			</div>
		</div>
	</page-box>
</template>

<script>

// 组件
import PageBox from '@/modules/page-box.vue';
import left0101 from '@/components/left0101.vue';
import left0102 from '@/components/left0102.vue';
import tableOrderDay from '@/components/tableOrderDay.vue';

import warnSum from '@/components/warnSum.vue';
import tableT01 from '@/components/tableT01.vue';
import warnToday from '@/components/warnToday.vue';
import warnAreaBar from '@/components/warnAreaBar.vue';
import warnAreaBarh from '@/components/warnAreaBarh.vue';

import warnOccurArea from '@/components/warnOccurArea.vue';
import warnOccurTime from '@/components/warnOccurTime.vue';
import tableT02 from '@/components/tableT02.vue';

// 页面数据
import pageJson from '@/../static/pageJson/aged.json';
// js
import {insertScript} from '@/assets/js/assets.com.js';
import {Common} from '@/assets/js/assets.com.js';

export default {
	name: '',
	components: {
		PageBox, 
		left0101, left0102, warnSum, tableT01, warnToday, tableOrderDay,
		warnAreaBar, warnAreaBarh, 
		warnOccurArea, warnOccurTime, tableT02
	}, 
	data () {
		return {
		  static: pageJson,
		  timer: '',
		  Common: Common,
		  warnOccurAreaOpt: {
		  	series: {
		  		name: "告警数"
		  	}
		  }
		}
	},
	mounted(){
		var timerI = 0;
		var _vue = this;
		this.queryData(timerI);

		this.timer = setInterval(function () {
			console.log( 'aged', timerI )
		  _vue.queryData(timerI);
		  timerI++;
		  timerI%=5;
		}, 30000);
	},
	beforeRouteLeave (to, from, next){
		clearInterval( this.timer );
		next();
	},
	methods: {
		queryData(index){
			var _vue = this;
			insertScript( `./static/data/aged/data${index}.js` );
			// console.log( $.extend(true, {}, pageJson, staticData ) );
			_vue.static = $.extend(true, {}, pageJson, staticData ) ;
		}
	}
	


}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="less">
.aged{
	div{
		box-sizing: border-box;
	}
	// 坐边
	.c_left{
		padding-left: 40px;
		padding-bottom: 15px;
		>div{
			float: left;
		}
		.left_all(){
			overflow: hidden;
			box-sizing: border-box;
		}
		.left_1{
			.left_all();
			width: 100%;
			height: 265px;
			margin-bottom: 15px;
			border-radius: 8px;
			border: solid 1px rgba(56, 174, 225, 0.71);
		}
		.left_2{
			.left_all();
			width: 400px;
			height: 675px;
			border: solid 1px rgba(56, 174, 225, 0.71);
			border-radius: 8px;
			.left_2_2{
				height: 415px;
				border: 1px solid #054453;
				border-radius: 10px;
			}
		}
		.left_3{
			.left_all();
			width: 800px;
			padding-left: 15px;
			height: 680px;
		}
		.left_3_1, .left_3_2{
			box-sizing: border-box;
			border: solid 1px rgba(56, 174, 225, 0.71);
			width: 100%;
			height: 330px;
			margin-bottom: 15px;
			border-radius: 8px;
		}
	}
	// 中间
	.c_center{
		padding: 0 20px 0 20px;
		>div{
			float: left;
		}
		.center_1{
			width: 1390px;
			height: 600px;
			margin-bottom: 20px;
			border-radius: 8px;
			// border: solid 1px rgba(56, 174, 225, 0.71);
		}
		.center_2{
			width: 690px;
			height: 330px;
			border-radius: 8px;
			border: solid 1px rgba(56, 174, 225, 0.71);
			margin-right: 10px;
		}
		.center_3{
			width: 690px;
			height: 330px;
			border-radius: 8px;
			border: solid 1px rgba(56, 174, 225, 0.71);
		}
	}
	// 右边
	.c_right{
		height: 100%;
		width: 100%;
		padding-right: 30px;
		padding-bottom: 15px;
		>div{
			float: left;
		}
		.right_1{
			width: 570px;
			height: 510px;
			margin-bottom: 15px;
			border-radius: 8px;
			border: solid 1px rgba(56, 174, 225, 0.25);
			overflow: hidden;
		}
		.right_2{
			width: 555px;
			height: 510px;
			margin-bottom: 15px;
			margin-left: 15px;
			border: solid 1px rgba(56, 174, 225, 0.25);
			border-radius: 8px;
			overflow: hidden;
		}
		.right_3{
			width: 1140px;
			height: 430px;
			border-radius: 8px;
			border: solid 1px rgba(56, 174, 225, 0.25);
		}
	}

	// 左边内容
	.left_1{
		>div{
			float: left;
		}
	}
	.left_2_2{

	}
	.center_2{
		padding-right: 10px;
		overflow: hidden;
	}
	.com_title{
		font-size: 30px;
		line-height: 60px;
		letter-spacing: 0px;
		color: #00fff0;
		padding-left: 40px;
	}

	// 中间
	.center_1{
		position: relative;
		.cont{
			width: 100%;
			height: 100%;
		}
		img{
			position: absolute;
			left: 0;
			right: 0;
			height: 100%;
			width: 100%;
		}
	}
}
	

</style>


