<!-- 月累计告警数 -->
<template>
	<div class="content">
	  <div class="title">
	    <img class="imgtit" src="../../static/img/yuegaojing.png" />
	    <span>{{ static.title }}</span>
	  </div>
	  <div class="container" style="">
	    <span>{{ static.desc }}</span>
	  </div>
	  <div class="container2">
	    <img src="../../static/img/ling.png" />
	    <a class="getvalue">{{ num }}</a>
	  </div>
	</div>
</template>

<script>
export default {
	name: '',
	props: {
		num: {
			default: '--'
		},
		static: {
			default(){
				return {
					"title": "月累计告警数",
					"desc": "总告警数"
				}
			}
		}
	},
	data () {
		return {
		  
		}
	}
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
	
  .content{
		margin:0;
		/*border:1px solid rgba(56, 174, 225, 0.71);*/
		height: 259px;
		width: 100%;
	}
	
  .title{
		font-size:30px;
		color:#00ffee;
		margin-top:14px;
		margin-bottom:80px;
	}
  .imgtit{
		float:left;
		margin-left:20px;
		margin-right:27px;
	}
  .container{
		height: 32px;
		overflow: hidden;
	}
  .container>span{
		float:left;
		color:#00ffee;
		font-size:24px;
		margin-left:47px;
	}
  .container2>img{
		margin-left:75px;
		float:left;
		margin-top:20px;
	}
  .getvalue{
		color:#fd4544;
		font-size: 36px;
		float:left;
		margin-left: 40px;
    font-weight: bold;
    position: relative;
    top: -10px;
    text-align: center;
    display: inline-block;
    width: 200px;
	}


	.warn_sum_sm{
		height: 200px ;
	}
	.warn_sum_sm .title{
		margin-bottom: 20px;
	}


</style>


