<!-- 日告警排名 -->
<template>
	<div class="warn_order_day">
		<div class="title">
			{{ static.title }}
		</div>
		<div>
			<table class="table">
				<tbody>
					<tr>
						<th v-for="item in static.thList">{{ item }}</th>
					</tr>
					<tr v-for="(item, index) in static.tdList">
						<td class="clo_1" :class="orderClass(index+1)">{{index+1}}</td>
						<td class="clo_2">{{item.alert_type}}</td>
						<td class="clo_3">
							<span class="num">
								{{item.num_alert_today}}<span class="icon" :class="item.tendency"></span>
							</span>
						</td>
						<td class="clo_4 num">{{item.num_alert_yesterday}}<span class="sml_txt">{{item.tendency_value}}</span></td>
					</tr>
				</tbody>
			</table>
		</div>
	</div>
</template>

<script>
export default {
	name: '',
	props: {
		static: {
			default(){
				return {
					"title": "日告警排名",
					"thList": ["排名", "告警类型", "当日告警数", "昨日告警数"],
					"tdList": [
						{
							"alert_type": "--",
							"num_alert_today": "--",
							"num_alert_yesterday": "--",
							"tendency": "--",
							"tendency_value": "--"
						}
					]
				}
			}
		}
	},
	data () {
		return {
		  
		}
	},
	methods:{
		orderClass (num){
			switch(num) {
				case 1:
					return 'order_1';
					break;
				case 2:
					return 'order_2';
					break;
				case 3:
					return 'order_3';
					break;
				default :
					return '';
					break;
			}
		} 
	}
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="less">
	@import url(../assets/less/var.less);
	.warn_order_day{
		height: 100%;
		border: 1px solid rgba(56, 174, 225, 0.71);
		border-radius: 10px;
	} 
	.title{
		font-size: 30px;
		line-height: 60px;
		letter-spacing: 0px;
		color: #00fff0;
		padding-left: 40px;
	}
	.num{
		font-size: 24px;
		font-weight: bold;
		position: relative;
		.icon{
			.icon( );
		}
	}
	.table{
		width: 100%;
		text-align: center;
		border: none;
	}
	.table tr:nth-child(odd){
		background-color: rgba(255, 255, 255, 0.05);
	}
	.table th, td{
		padding: 10px 0;
		font-size: 14px;
		color: #e0e0e0;
	}
	.clo_1{
		font-size: 24px;
		position: relative;
		.order(@url){
			content: "";
			position: absolute;
			top: 0;
			left: 0;
			right: 0;
			bottom: 0;
			background: url(@url) no-repeat center;
			background-size: 35px;
		}
		&.order_1::after{
			// .order('../../static/img/order_1.png');
		}
		&.order_2::after{
			// .order('../../static/img/order_2.png');
		}
		&.order_3::after{
			// .order('../../static/img/order_3.png');
		}

	}
	.sml_txt{
		font-size: 12px;
		font-weight: normal;
		display: inline-block;
		padding: 0 0 0 20px;
		color: #e41902;
	}
	.table th{
		color: #b4b4b4;
		font-size: 14px;
	}
	.table tr:nth-child(2) td{
		color: #d5b930;
	}


</style>


