<!-- 告警高发小区排名： -->
<template>
	<div class="content">
    <div class="title">
      <span>{{ static.title }}</span>
    </div>
    <div class="container" style=""></div>
	</div>
</template>

<script>
import {arrayCopy} from '../assets/js/assets.com.js';

function initChart( ele, list ){
	
	var myData = formatList( list);

	var myChart ;
	var dom = ele.querySelector('.container');
	if ( $(dom).find('canvas').length > 0 ) {
	  myChart = echarts.getInstanceByDom( dom )
	}else{
	  myChart = echarts.init( dom );
	}

	var option = null;
	option={
	    
	    tooltip: {
	    	trigger: 'axis',
	    	axisPointer: {
	    		type: 'shadow'
	    	},
	    	formatter: '{b0}: {c0}',
	    },
	    grid: {
	        left: '0%',
	        right: '5%',
	        top: '0%',
	        bottom: '3%',
	        containLabel: true
	    },
	    xAxis: {
	        show:true,
	        nameLocation:"end",
	        
	        nameTextStyle:{
	            color:"#999"
	        },
	        data: myData.xList,
	        axisLabel: {
	            inside: false,
	            textStyle: {
	              color: '#eaeaea',
								fontSize:14,
	            }
	        },
	        axisTick: {
	            show: false
	        },
	        axisLine: {
	            show: false
	        },
	        z: 10
	    },
	    yAxis: {
	        show:false,
	        nameLocation:"end",
	        name:"(用户数)",
	        nameTextStyle:{
	            color:"#999"
	        },
	        axisLine: {
	            show: false
	        },
	        axisTick: {
	            show: false
	        },
	        axisLabel: {
	            textStyle: {
	                color: '#999'
	            },
	            formatter: function (value) {
	                var a=parseFloat(value/10000,2)
	                return a+"万";
	            }
	        },
	        splitLine:{
	            show:false
	        }
	    },
	    series: [
	        {
	            name: 'line',
	            type: 'bar',
	            barGap: '-100%',
				barCategoryGap:'30%',
	            barWidth: 27,
	            itemStyle: {
	                normal: {
	                    color: new echarts.graphic.LinearGradient(
	                        0, 0, 0, 1,
	                        [
	                            {offset: 1, color: 'rgba(72,176,227,1)'},
	                            {offset: 0, color: 'rgba(234,70,118,1)'},
	                        ]
	                    )
	                }
	            },
	            z: -4,
	            label:{
	                emphasis:{
	                    show:false,
	                }
	            },
	            data: myData.data1
	        },
	        {
	            name: 'dotted',
	            type: 'pictorialBar',
	            symbol: 'rect',
	            itemStyle: {
	                normal: {
	                    color: "#002255"
	                },
	                emphasis: {
	                    color:"#002255"
	                }
	            },
	            symbolRepeat: true,
	            symbolSize: [28,3],
	            symbolMargin: 2,
	            z: -2,
	            data: myData.data2
	        }
	    ]
	};
	
	if (option && typeof option === "object") {
	    myChart.setOption(option, true);
	}
}

function formatList( list ){
	var _list = arrayCopy( list );
	_list.sort( function(item1, item2) {
		return item2.num_alert - item1.num_alert
	});
	var xList = [];
	var data1 = [];
	var data2 = [];
	_list.forEach( function(item, index) {
		xList.push( item.district_name );
		data1.push( item.num_alert );
		data2.push( _list[0].num_alert );
	});
	return {
		xList,
		data1,
		data2
	}
}

export default {
	name: '',
	props: {
		list: {
			default (){
				return aged1.day_alert_district_topn
			}
		},
		static: {
			default(){
				return {
					"title": "告警高发小区排名："
				}
			}
		}
	},
	data () {
		return {
		  
		}
	},
	watch: {
		list(val, oldV){
			initChart( this.$el, this.list );
		}
	},
	mounted (){
		initChart( this.$el, this.list );
	}
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
	.title{
		padding-left:86px;
		padding-top:22px;
		font-size:30px;

		color:#00ffee;
	}
	.content{
		margin:0;
		/*border:1px solid rgba(56, 174, 225, 0.71);*/
		height: 330px;
		width:690px;
		background-color: rgba(4, 34, 65, 0.5);
		border-radius: 8px;
	}
	.container{
		margin-top:27px;
		height: 230px;
		width: 680px;
		padding: 0 10px;
	}
</style>


